.window-centering{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-width: 100vw;
    min-height: 100vh;
    background-color: #b7e4c7;
}
.auth-window{
    width: 350px;
    height: 450px;
    border-radius: 10px;
    background-color: #52b788;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.register-page .auth-window{
    width: 350px;
    height: 450px;
}
.auth-title{
    font-size: 28px;
    margin-top: 40px;
    margin-bottom: 30px;
    font-weight: bold;
    color: #1b4332;
}
.auth-button{
    width: 300px;
    height: 50px;
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 30px;
    font-size: 20px;
    outline: none;
    border: none;
    background-color: #459671;
    color: #e0fbe3;
}
.auth-button:hover{
    font-size: 21px;
    background-color: #40916c;
}
.auth-input{
    width: 280px;
    height: 50px;
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 30px;
    font-size: 16px;
    outline: none;
    border: none;
    padding-left: 20px;
    color: #1b4332;
}
.row-buttons{
    display: flex;
    flex-direction: row;
}
.side-button{
    margin-top: 100px;
    outline: none;
    border: none;
    padding: none;
    margin-inline: 5px;
    font-size: 14px;
    color: #1b4332;
    background-color: #00000000;
}
.side-button:hover{
    font-weight: bold;
    margin-inline: 0px;
    margin-left: 3px;
    margin-right: -3px;
}
.register-page .side-button{
    margin-top: 20px;
    margin-right: 220px;
}